.subtitle-and-line {
  display: flex;
  align-items: center;
  margin: 0;
}
.subtitle-and-line::before {
  content: "";
  display: inline-block;
  position: relative;
  border-top: 2px solid #68b738;
  width: 100%;
}
@media (max-width: 767px) {
  .subtitle-and-line {
    font-size: 24px;
    line-height: 24px;
    padding-top: 25px;
    padding-bottom: 36px;
    flex-direction: row-reverse;
  }
  .subtitle-and-line::before {
    left: 20px;
  }
}
@media (min-width: 768px) and (max-width: 1199px) {
  .subtitle-and-line {
    font-size: 36px;
    line-height: 36px;
    padding-bottom: 80px;
    padding-top: 68px;
    flex-direction: row-reverse;
  }
  .subtitle-and-line::before {
    left: 36px;
  }
  .subtitle-and-line::after {
    content: "";
    display: inline-block;
    position: relative;
    border-top: 2px solid #68b738;
    width: 60px;
    right: 36px;
  }
}
@media (min-width: 1200px) {
  .subtitle-and-line {
    font-size: 36px;
    line-height: 36px;
    justify-content: center;
    width: fit-content;
    background-color: #ffffff;
    padding: 76px 36px;
    margin-left: 70px;
  }
  .subtitle-and-line::before {
    width: 100%;
    position: absolute;
    z-index: -1;
    right: 0;
  }
  .subtitle-and-line::after {
    width: 100%;
    position: absolute;
    z-index: -1;
    left: -110px;
  }
}
